clearInterval(this.$options._interval)\n\n          this.$nextTick(() => {\n            if (!this.file.error) {\n              setTimeout(() => this.$refs.downloadLink.click(), 100)\n            }\n          })\n        }\n      } catch (e) {\n        clearInterval(this.$options._interval)\n\n        console.error(e)\n      }\n    },\n  },\n}\n</script>\n\n<style scoped>\n.dialog {\n  position: fixed;\n  bottom: 10px;\n  right: 10px;\n  width: 400px;\n  max-width: 90%;\n}\n\n.hover\\:bg-light:hover {\n  background-color: #dddddd;\n}\n</style>\n"],"sourceRoot":""}]);
